@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "iconfont";
  src: url("../fonts/iconfont.eot");
  src: url("../fonts/iconfont.eot?#iefix") format("eot"), url("../fonts/iconfont.woff2") format("woff2"), url("../fonts/iconfont.woff") format("woff"), url("../fonts/iconfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
}

.icon-out:before {
  content: "";
}

.icon-search:before {
  content: "";
}

.block {
  display: flex;
}

@font-face {
  font-family: RobotoMedium;
  src: url("../fonts/RobotoMedium.woff2") format("woff2"), url("../fonts/RobotoMedium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: RobotoRegular;
  src: url("../fonts/RobotoRegular.woff2") format("woff2"), url("../fonts/RobotoRegular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: RobotoBlack;
  src: url("../fonts/RobotoBlack.woff2") format("woff2"), url("../fonts/RobotoBlack.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-size: 13px;
  min-width: 1024px;
  font-family: RobotoRegular, Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
}

.indicator {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #36d1dc;
  margin-left: 3px;
}

.indicator__in {
  position: absolute;
  font-size: 10px;
  color: #fff;
  z-index: 2;
  line-height: normal;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
}

.reg {
  font-family: RobotoRegular, Arial, Helvetica, sans-serif;
}

.medium {
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
}

.wtite-color {
  color: #fff;
}

.wrapper {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
}

.wrapper__sidebar {
  width: 340px;
  flex: 0 0 auto;
}

.wrapper__content {
  flex: 1 1 auto;
}

.icon-search {
  font-size: 18px;
  color: #4990e2;
}

.icon-clip {
  font-size: 18px;
  color: #000;
}

.gray-color {
  color: #4a4a4a;
}

.link {
  color: #4990e2;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
  transition: opacity 0.3s;
}

.link:hover, .link:active {
  opacity: .8;
}

.header {
  position: relative;
  background-image: linear-gradient(86deg, #5b86e5, #36d1dc);
  max-height: 80px;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.header__logo {
  padding: 28px 0;
  width: 340px;
  text-align: center;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  float: left;
}

.header__nav {
  float: left;
}

.header__right {
  float: right;
  padding: 20px 0 10px 0;
  margin-right: 30px;
  white-space: nowrap;
}

.logo {
  font-size: 18px;
  letter-spacing: 7.5px;
  color: #fff;
  text-transform: uppercase;
  font-family: RobotoBlack, Arial, Helvetica, sans-serif;
  transition: opacity 0.3s;
}

.logo:hover {
  opacity: .8;
}

.nav {
  font-size: 0;
}

.nav__item {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  transition: background 0.3s;
}

.nav__item.is-active, .nav__item:hover {
  background: rgba(0, 0, 0, 0.1);
}

.nav__item.is-active:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 5px 5px 5px;
  border-color: transparent transparent #fff transparent;
}

.nav__item:last-child {
  margin-left: 80px;
}

@media only screen and (max-width: 1280px) {
  .nav__item:last-child {
    margin-left: 0;
  }
}

.nav__link {
  display: inline-block;
  padding: 29px 26px;
  font-size: 15px;
  color: #fff;
}

@media only screen and (max-width: 1100px) {
  .nav__link {
    padding: 29px 20px;
  }
}

.nav__link span {
  display: inline-block;
  vertical-align: middle;
  max-width: 90px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.hidden-block {
  display: none;
  margin-left: 340px;
  background: #FFF;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  z-index: 99;
  box-shadow: -1px 2px 5px 0 #f0f0f0;
}

.hidden-block.is-open {
  display: block;
}

.hidden-block__header {
  padding: 20px 20px 20px 30px;
  border-bottom: 1px solid #f0f0f0;
}

.hidden-block__header:after {
  content: " ";
  display: table;
  clear: both;
}

.hidden-block__header-left {
  float: left;
  width: 60%;
}

.hidden-block__header-right {
  float: left;
  width: 40%;
}

.hidden-block__content {
  padding: 23px 30px;
}

.hidden-block__content:after {
  content: " ";
  display: table;
  clear: both;
}

.hidden-block__link {
  float: left;
  width: 25%;
}

.sign-in {
  display: inline-block;
  vertical-align: middle;
  margin-right: 70px;
}

.sign-in:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1080px) {
  .sign-in {
    margin-right: 20px;
  }
}

.sign-in__ava {
  float: left;
  margin-right: 18px;
}

.sign-in__text {
  display: inline-block;
  overflow: hidden;
  padding: 4px 0;
  color: #fff;
}

.ava {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
}

.ava__pic {
  max-width: 100%;
}

.out {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  transition: opacity 0.3s;
}

.out:hover {
  opacity: .8;
}

.out:active {
  color: rgba(0, 0, 0, 0.1);
}

.sidebar__header {
  white-space: nowrap;
  overflow: hidden;
}

.sidebar__header-btn, .sidebar__header-input {
  display: inline-block;
  vertical-align: top;
}

.sidebar__header-btn {
  background: #FFF;
  padding: 33px 30px;
  max-width: 130px;
  overflow: hidden;
}

.sidebar__header-input {
  background: #f0f0f0;
  padding: 33px 35px;
}

.sidebar__body {
  position: relative;
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: #f0f0f0;
  padding: 40px 15px 40px 40px;
  height: calc(100vh - 170px);
  overflow-Y: auto;
}

.sidebar__body:after {
  content: "";
  position: absolute;
  right: 0;
  top: 10px;
  width: 15px;
  bottom: 10px;
  z-index: 5;
  box-shadow: -5px 0px 11px 0px #fff;
}

.input {
  position: relative;
}

.input_big input {
  padding-right: 100px;
}

.input input {
  display: inline-block;
  vertical-align: middle;
  background: transparent;
  width: 100%;
  border: none;
  color: #000;
  font-size: 12px;
  letter-spacing: 1px;
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
}

.input input::placeholder {
  color: #d8d8d8;
}

.input__icon {
  position: absolute;
  z-index: 2;
  right: 2px;
  top: 1px;
  padding: 1px;
}

.input_pic input {
  width: calc(100% - 25px);
}

button {
  background: transparent;
  border: none;
  transition: opacity 0.3s;
}

button:hover {
  opacity: .8;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  border-radius: 5px;
  background-color: #4990e2;
  font-size: 12px;
  padding: 13px 10px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  min-width: 160px;
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
  transition: opacity 0.3s;
}

.btn + .btn {
  margin-left: 20px;
}

.btn:hover, .btn:active {
  opacity: .7;
}

.client {
  display: inline-block;
  vertical-align: top;
}

.client:not(:last-child) {
  margin-bottom: 20px;
}

.client:hover .client__about {
  opacity: .7;
}

.client p {
  font-size: 13px;
}

.client__ava {
  float: left;
  margin-right: 30px;
}

.client__about {
  overflow: hidden;
}

.client__about-text {
  white-space: nowrap;
}

.client__header {
  position: relative;
}

.client__header:after {
  content: " ";
  display: table;
  clear: both;
}

.client__header-left {
  padding-right: 30px;
}

.client__header-left p {
  color: #000;
}

.client__header-right {
  position: absolute;
  right: 5px;
  top: 0;
  z-index: 10;
}

.timeline__item {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  background: transparent;
  border-radius: 10px;
  padding: 4px 13px;
  line-height: normal;
  font-size: 10px;
  color: rgba(0, 0, 0, 0.2);
  pointer-events: none;
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
  transition: opacity 0.3s;
}

.timeline__item:not(:last-child) {
  margin-right: 20px;
}

.timeline__item.is-active {
  background: #36d1dc;
  color: #fff;
}

.timeline__item.is-active:not(:first-child):before {
  content: '';
  position: absolute;
  left: -12px;
  top: 6px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 3px 0 3px 4px;
  border-color: transparent transparent transparent #36d1dc;
}

.chat__in {
  min-height: 280px;
  padding: 35px 0;
}

.chat__item {
  position: relative;
  margin: 0 90px;
}

.chat__item_right {
  text-align: right;
}

.chat__item_right .text {
  background: #f0f0f0;
}

.chat__item_left {
  text-align: left;
}

.chat__item_left .text {
  border: 1px solid #f0f0f0;
  background: #fff;
}

.chat__item_arr .text {
  position: relative;
}

.chat__item_arr .text:after {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: -4px;
  z-index: 2;
}

.chat__item_arr-right .text:after {
  left: 100%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 0 4px 4px;
  border-color: transparent transparent transparent #f0f0f0;
}

.chat__item_arr-left .text:after {
  right: 100%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 5px 5px 5px 0;
  border-color: transparent #f0f0f0 transparent transparent;
}

.chat__item_arr-left .text:before {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: -3px;
  z-index: 3;
  left: -4px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 5px 4px 0;
  border-color: transparent #fff transparent transparent;
}

.chat__divider {
  padding: 22px 29px;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.chat__time {
  position: absolute;
  left: calc(100% + 25px);
  top: 14px;
  font-size: 10px;
  color: #d8d8d8;
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
}

.chat__ava {
  position: absolute;
  right: calc(100% + 25px);
  top: 0;
}

.text {
  display: inline-block;
  vertical-align: middle;
  color: #000;
  font-size: 13px;
  padding: 12px 20px;
  border-radius: 5px;
  margin-bottom: 10px;
  font-family: RobotoMedium, Arial, Helvetica, sans-serif;
}

.chat-footer {
  position: fixed;
  left: 340px;
  right: 15px;
  bottom: 0;
  padding: 32px 52px 32px 29px;
  border-top: 1px solid #f0f0f0;
  background: #FFF;
}

.chat-footer:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1070px) {
  .chat-footer {
    right: 0;
  }
}

.chat-footer__btns {
  position: absolute;
  right: 52px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.chat-wrapper {
  position: relative;
  height: calc(100vh - 81px);
  overflow-Y: auto;
  padding: 81px 0 83px 0;
}

.clip {
  display: inline-block;
  vertical-align: top;
  width: 20px;
  height: 20px;
  background: url("../img/clip.svg") no-repeat;
  background-size: contain;
  margin: -2px 30px 0 0;
}
